import { useEffect, useState } from 'react';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';
import EastIcon from '@mui/icons-material/East';
import FavoriteIcon from '@mui/icons-material/Favorite';
import '../components/Animes';
import axios from 'axios';
import '../scss/Animes.scss'

function Animes() {
  const [search , setSearch] = useState();
  const [query, setQuery] = useState([]);
  const [pages ,setPages] = useState(0)
  const [length,setLength] = useState(0)

  console.log(query.length)

  const fetchData = async () =>{
    try{
      const res = await axios.get(`https://api.jikan.moe/v4/characters?page=${pages}&limit=15&q=${search}&order_by=favorites&sort=desc`)
      const anime = res.data.data
      setQuery(anime)
   

      const len = await axios.get(`https://api.jikan.moe/v4/characters?q=${search}&order_by=favorites&sort=desc`)
      if(search){
        console.log(setLength(len.data.data.length))
      }
      else{
        setLength(0)
      }

    }catch(err){
      
    }
}
  useEffect(()=>{
    let debounce = setTimeout(() => {
        fetchData();
    }, 2000);
    return ()=> clearTimeout(debounce)
  },[search,pages]);

const pageHandlerInc = (e)=>{
  if(pages >=0){
    setPages(pages+1)
  }
  
}
const pageHandlerDec = (e)=>{
if(pages >= 1){
  setPages(pages-1)
}
else{
  setPages(0)
}
}

  return (
    <>
    <section className="header ">
      <p className='head'>  Search Anime Characters</p>
       <div className="search-bar">
        <input type="text" placeholder='search anime character' name="search" className="inputEl" onChange={event => setSearch(event.target.value)}  value={search}/>
        <button type="submit">
       <SearchOutlinedIcon className='btn'style={{fontSize:40,color:"white"}}/> 
        </button>
      </div>
      <p className='arrLength'>{`Total ${length} matching anime characters found`}</p>
    </section>
    <section className='content'>{
        query.length ?
    
           search && query.map((el)=>{
return<>

            <section className='content-box' key={el.mal_id}>
              <div className='content-box--img'>
                <img src={el.images.jpg.image_url} alt="" />
              </div>
              <div className='content-box--names'>
                <div className='content-box--names-inside'>
                  <div className= "content-box--names-name">
                  <p>{el.name}</p>
                  <p><span><FavoriteIcon className='heart'/></span><span>{new Intl.NumberFormat('en-US').format(el.favorites)}</span></p>
                  
                  </div>
                   
                  <div className="content-box--names-nicknames">{
                    el.nicknames.map((nick)=>{
                   return <span className='nickname'>{nick}</span>
                    })
                  }
                  </div>
                  
                </div>
                {/* <div className='content-box--names-favorites'>
                  <p><span><FavoriteIcon className='heart'/></span><span>{el.favorites}</span></p>
                </div> */}
              </div>
              <a href={`/animedetails/${el.mal_id}`}>

              <div className='content-box--btn'>
              <EastIcon className='arrow'/>
              </div>
              </a>
            </section>
</>
          }):null
          }

    </section>

    <section className='button-box'>{
        search && <>
            <button className='button-box--btn' onClick={pageHandlerDec}>Back</button>
            <button className='button-box--btn' onClick={pageHandlerInc}>Next</button>
        </>
    }
    </section>


    </>
  );
}

export default Animes;
